import axios from 'axios';

const url = '/api/imdbid/'

class ImdbidService {

    //Get Shapes
    static getMovieIds(query) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}`, {
                    params: {
                        year: query.year,
                        start: query.start,
                        count: query.count,
                    }
                });
                const data = res.data;
                resolve(data);
            } catch (err) {
                reject(err);
            }
        });
    }
}

export default ImdbidService;