import axios from 'axios';

const url = 'http://www.omdbapi.com/?apikey=6fed019'

class OmdbService {

    //Get Shapes
    static getMovieData(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}`, {
                    params: {
                        i: id,
                    }
                });
                const data = res.data;
                resolve(data);
            } catch (err) {
                reject(err);
            }
        });
    }
}

export default OmdbService;