<script>

import Poster from './../components/Poster'

import OmdbService from './../OmdbService'
import MovieService from './../MovieService'
import ImdbidService from './../ImdbidService'
// import WindowService from './../WindowService'
import GetPosterService from './../GetPosterService'

export default {
  name: 'Movies',
  data() {
    return {
      edit: '',
      filterBy: 'year',
      imdbId: '',
      url: '',
      imdbYear: '',
      imdbStart: '1',
      imdbCount: '10',
      result: [],
      moviesToBeAdded: [],
      allMovies: null,
      // newMovie: null,
      filterYear: null,
      filterMood: 'Anger',
      filterImdbId: '',
      filterTitle: '',
      filterPage: 1,
      filterCount: 20,
      // id: '5d20f7877aaaa1b5b9b04fa1',
      // posterData: null,
      response: null,
      windows: null,
      primeIds: [],
      error: '',
    }
  },
  components: {
    Poster

  },
  computed: {
    resultCount() {
      return this.allMovies.length
    }
  },
  created() {
    this.getMovies()
  },
  methods: {
    async getNewMovieData(id) {
      this.newMovie = await OmdbService.getMovieData(id)
      await MovieService.insertMovie({
        imdbId: id,
        Actors: this.newMovie.Actors,
        Awards: this.newMovie.Awards,
        BoxOffice: this.newMovie.BoxOffice,
        Country: this.newMovie.Country,
        DVD: this.newMovie.DVD,
        Director: this.newMovie.Director,
        Genre: this.newMovie.Genre,
        Language: this.newMovie.Language,
        Metascore: this.newMovie.Metascore,
        Plot: this.newMovie.Plot,
        Poster: this.newMovie.Poster,
        Production: this.newMovie.Production,
        Rated: this.newMovie.Rated,
        Ratings: this.newMovie.Ratings,
        Released: this.newMovie.Released,
        Response: this.newMovie.Response,
        Runtime: this.newMovie.Runtime,
        Title: this.newMovie.Title,
        Type: this.newMovie.Type,
        Website: this.newMovie.Website,
      })
      this.getMovies()
    },
    async getMovies() {
      if (this.filterBy === 'year') {
        this.allMovies = await MovieService.getMovies({
          by: this.filterBy,
          year: this.filterYear,
          count: this.filterCount,
          page: this.filterPage,
        })
      }
      if (this.filterBy === 'imdbId') {
        this.allMovies = await MovieService.getMovies({
          by: this.filterBy,
          imdbId: this.filterImdbId,
        })
      }
      if (this.filterBy === 'title') {
        this.allMovies = await MovieService.getMovies({
          by: this.filterBy,
          title: this.filterTitle,
          count: this.filterCount,
          page: this.filterPage,
        })
      }
      if (this.filterBy === 'mood') {
        this.allMovies = await MovieService.getMoviesByMood({
          by: this.filterBy,
          mood: this.filterMood,
          count: this.filterCount,
          page: this.filterPage,
        })
      }
      if (this.filterBy === 'prime') {
        this.allMovies = await MovieService.getMovies({
          by: this.filterBy,
          count: this.filterCount,
          page: this.filterPage,
        })
      }
      
    },
    async updateMoviePoster(id) {
      const path = 'https://mood2watch.s3.amazonaws.com/' + this.response
      await MovieService.updateMoviePoster({
        id: id,
        poster: path,
      })
      this.getMovies()
    },
    async deleteMovie(id) {
      await MovieService.deleteMovie(id)
      this.getMovies()
    },
    async scrapeIds() {
      this.result = await ImdbidService.getMovieIds({
        year: this.imdbYear,
        start: this.imdbStart,
        count: this.imdbCount,
      })
      let arrayLength = this.result.length;
      for (var i = 0; i < arrayLength; i++) {
          let obj = await OmdbService.getMovieData(this.result[i])
          this.moviesToBeAdded.push(obj)
      }
      await MovieService.addManyMovies(this.moviesToBeAdded)
      this.getMovies()
      this.moviesToBeAdded = []
    },
    async getPoster(payload) {
      this.response = await GetPosterService.getPoster(payload)
      this.updateMoviePoster(payload.id)
    },
    async applyFilter() {
      if (this.filterBy === 'prime') {
        this.getMovies()
      }
    }
  }
}
</script>

